'use client';

import { Task, useTaskStore } from '@/store/useTaskStore';
import { Draggable } from '@hello-pangea/dnd';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Trash2 } from 'lucide-react';
import { motion } from 'framer-motion';
import { cn } from '@/lib/utils';

interface TaskCardProps {
    task: Task;
    index: number;
    onEdit: (task: Task) => void;
}

export function TaskCard({ task, index, onEdit }: TaskCardProps) {
    const deleteTask = useTaskStore((state) => state.deleteTask);

    return (
        <Draggable draggableId={task.id} index={index}>
            {(provided, snapshot) => (
                <div
                    ref={provided.innerRef}
                    {...provided.draggableProps}
                    {...provided.dragHandleProps}
                    style={{ ...provided.draggableProps.style }}
                    className={cn('mb-3', snapshot.isDragging && 'opacity-80')}
                >
                    <motion.div
                        layout
                        initial={{ opacity: 0, y: 10 }}
                        animate={{ opacity: 1, y: 0 }}
                        exit={{ opacity: 0, scale: 0.9 }}
                    >
                        <Card
                            className="group relative hover:shadow-md transition-shadow cursor-pointer active:cursor-grabbing border-l-4 border-l-primary/50 hover:border-l-primary"
                            onClick={() => onEdit(task)}
                        >
                            <CardHeader className="p-3 pb-0 flex flex-row justify-between items-start space-y-0">
                                <CardTitle className="text-sm font-medium leading-tight">
                                    {task.title}
                                </CardTitle>
                                <Button
                                    variant="ghost"
                                    size="icon"
                                    className="h-6 w-6 text-muted-foreground opacity-0 group-hover:opacity-100 transition-opacity absolute top-2 right-2"
                                    onClick={(e) => {
                                        e.stopPropagation();
                                        deleteTask(task.id);
                                    }}
                                >
                                    <Trash2 className="h-3 w-3" />
                                </Button>
                            </CardHeader>
                            <CardContent className="p-3 pt-2">
                                {task.description && (
                                    <p className="text-xs text-muted-foreground line-clamp-2">
                                        {task.description}
                                    </p>
                                )}
                                <div className="mt-2 text-[10px] text-muted-foreground text-right">
                                    {new Date(task.createdAt).toLocaleDateString()}
                                </div>
                            </CardContent>
                        </Card>
                    </motion.div>
                </div>
            )}
        </Draggable>
    );
}
