'use client';

import { useState } from 'react';
import { Board } from '@/components/board/Board';
import { TaskForm } from '@/components/board/TaskForm';
import { useTaskStore, Task } from '@/store/useTaskStore';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Plus, Search, LayoutTemplate } from 'lucide-react';

export default function Home() {
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [taskToEdit, setTaskToEdit] = useState<Task | null>(null);

  const searchQuery = useTaskStore((state) => state.searchQuery);
  const setSearchQuery = useTaskStore((state) => state.setSearchQuery);

  const handleAddTask = () => {
    setTaskToEdit(null);
    setIsDialogOpen(true);
  };

  const handleEditTask = (task: Task) => {
    setTaskToEdit(task);
    setIsDialogOpen(true);
  };

  return (
    <main className="min-h-screen bg-background flex flex-col font-sans text-foreground">
      {/* Header */}
      <header className="px-6 py-4 flex flex-col md:flex-row items-center justify-between border-b gap-4 bg-white/80 backdrop-blur-md sticky top-0 z-10">
        <div className="flex items-center gap-2">
          <div className="bg-primary/10 p-2 rounded-lg">
            <LayoutTemplate className="w-6 h-6 text-primary" />
          </div>
          <h1 className="text-xl font-bold tracking-tight text-foreground">
            Tasker
          </h1>
        </div>

        <div className="flex items-center gap-4 w-full md:w-auto">
          <div className="relative w-full md:w-80">
            <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
            <Input
              placeholder="Search tasks..."
              className="pl-9 bg-secondary/50 border-none focus-visible:ring-1 focus-visible:ring-primary"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
            />
          </div>
          <Button onClick={handleAddTask} className="shadow-lg shadow-primary/20 hover:shadow-primary/40 transition-shadow">
            <Plus className="w-4 h-4 mr-2" />
            New Task
          </Button>
        </div>
      </header>

      {/* Board Content */}
      <div className="flex-1 overflow-hidden p-6">
        <Board onEditTask={handleEditTask} />
      </div>

      {/* Task Form Dialog */}
      <TaskForm
        open={isDialogOpen}
        onOpenChange={setIsDialogOpen}
        taskToEdit={taskToEdit}
      />
    </main>
  );
}
