'use client';

import { Task, TaskStatus } from '@/store/useTaskStore';
import { Droppable } from '@hello-pangea/dnd';
import { TaskCard } from './TaskCard';
import { cn } from '@/lib/utils';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/card';

interface ColumnProps {
    id: TaskStatus;
    title: string;
    tasks: Task[];
    onEdit: (task: Task) => void;
}

export function Column({ id, title, tasks, onEdit }: ColumnProps) {
    return (
        <Card className="h-full flex flex-col bg-muted/50 border-none shadow-sm">
            <CardHeader className="p-4 pb-2">
                <CardTitle className="text-sm font-semibold uppercase tracking-wider text-muted-foreground flex justify-between items-center">
                    {title}
                    <span className="bg-background text-foreground px-2 py-0.5 rounded-full text-xs shadow-sm border">
                        {tasks.length}
                    </span>
                </CardTitle>
            </CardHeader>
            <CardContent className="p-2 flex-1 overflow-y-auto min-h-[500px]">
                <Droppable droppableId={id}>
                    {(provided, snapshot) => (
                        <div
                            {...provided.droppableProps}
                            ref={provided.innerRef}
                            className={cn(
                                'h-full rounded-md transition-colors p-1',
                                snapshot.isDraggingOver ? 'bg-primary/5' : 'bg-transparent'
                            )}
                        >
                            {tasks.map((task, index) => (
                                <TaskCard
                                    key={task.id}
                                    task={task}
                                    index={index}
                                    onEdit={onEdit}
                                />
                            ))}
                            {provided.placeholder}
                        </div>
                    )}
                </Droppable>
            </CardContent>
        </Card>
    );
}
